// redux toolkit  createSlice reducer action, payload  
import { createSlice } from '@reduxjs/toolkit';

export const userSlice = createSlice({
	name: 'user',
	initialState: {
		displayName: '',
		uid: '',
		accessToken: '',
	},
	reducers: {
		loginUser: (state, action) => {
			state.displayName = action.payload.displayName;
			state.uid = action.payload.uid;
			state.accessToken = action.payload.accessToken;
		},
		logoutUser: (state) => {
			state.displayName = '';
			state.uid = '';
			state.accessToken = '';
		},
	},
});

// createSlice  2 loginUSer, logoutUser  
export const { loginUser, logoutUser } = userSlice.actions; // reducers   actions 
export default userSlice.reducer; //    
