import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import firebase from '../../firebase';
import axios from 'axios';

function Join() {
	const navigate = useNavigate();
	const [email, setEmail] = useState('');
	const [pw1, setPw1] = useState('');
	const [pw2, setPw2] = useState('');
	const [name, setName] = useState('');
	//     state
	const [nameCheck, setNameCheck] = useState(false);
	//     state
	const [nameInfo, setNameInfo] = useState('');

	const handleNameCheck = () => {
		if (!name) {
			return alert(' ');
		}
		const body = {
			displayName: name,
		};

		axios.post('/api/user/nameCheck', body).then((res) => {
			if (res.data.success) {
				//   DB 
				if (res.data.check) {
					setNameCheck(true);
					setNameInfo(' .');
				} else {
					setNameInfo(' .');
				}
			}
		});
	};
	const handleJoin = async () => {
		if (!(name && email && pw1 && pw2)) {
			return alert('  ');
		}
		if (pw1 !== pw2) {
			return alert('  ');
		}
		// firebase   6
		if (pw1.length < 6) {
			return alert('  6  ');
		}
		// nameCheck true 
		if (!nameCheck) {
			return alert('  .');
		}

		//         
		//  await firebase       
		let createdUser = await firebase
			.auth()
			.createUserWithEmailAndPassword(email, pw1);

		await createdUser.user.updateProfile({
			displayName: name,
		});

		console.log(createdUser); // user.multiFactor.user 

		//    axios    
		const body = {
			email: createdUser.user.multiFactor.user.email,
			displayName: createdUser.user.multiFactor.user.displayName,
			uid: createdUser.user.multiFactor.user.uid,
		};

		axios.post('/api/user/join', body).then((res) => {
			if (res.data.success) {
				//
				navigate('/login');
			} else {
				//  
				return alert(' .');
			}
		});
	};

	return (
		<section id='join'>
			<div className='inner'>
				<article>
					<h2>join member</h2>
					<input
						type='email'
						value={email}
						placeholder=' '
						onChange={(e) => setEmail(e.target.value)}
					/>
					<br />
					<input
						type='password'
						value={pw1}
						placeholder=' '
						onChange={(e) => setPw1(e.target.value)}
					/>
					<br />
					<input
						type='password'
						value={pw2}
						placeholder='  '
						onChange={(e) => setPw2(e.target.value)}
					/>
					<br />
					<input
						type='text'
						value={name}
						placeholder=' '
						onChange={(e) => setName(e.target.value)}
					/>
					<button onClick={handleNameCheck} className='btnCheck'>
						
					</button>
					{nameInfo}
					<br />
					<button onClick={handleJoin}></button>
				</article>
			</div>
		</section>
	);
}

export default Join;
