import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';
import 'moment/locale/ko';

function List(props) {
	const [list, setList] = useState([]);
	const [loaded, setLoaded] = useState(false);
	const [sort, setSort] = useState('new');
	const [search, setSearch] = useState('');

	const getList = () => {
		let num = 0;
		if (props.count) num = props.count;
		const body = {
			sort: sort,
			search: search,
			count: num,
		};
		axios
			.post('/api/post/list', body)
			.then((res) => {
				console.log(res);
				if (res.data.success) {
					console.log(res.data);
					setList(res.data.postList);
					setLoaded(true);
				}
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const handleSearch = () => {
		getList();
	};
	//    
	useEffect(getList, [sort]);

	return (
		<section id='list'>
			<div className='inner'>
				{!loaded && <img src={`${process.env.PUBLIC_URL}/img/loading.gif`} />}

				{!props.hideSearch && (
					<>
						<h1>list</h1>
						<button
							onClick={() => {
								setSort('new');
							}}>
							
						</button>
						<button
							onClick={() => {
								setSort('old');
							}}>
							
						</button>
						<input
							type='text'
							value={search}
							onChange={(e) => setSearch(e.target.value)}
							onKeyUp={(e) => {
								if (e.keyCode === 13) handleSearch();
							}}
						/>
					</>
				)}

				{list.map((post, idx) => {
					console.log(post);
					return (
						// <>  React.Fragment 
						<React.Fragment key={idx}>
							<article>
								<h2>
									{/*   postNum   url */}
									<Link to={`/post/${post.postNum}`}>{post.title}</Link>
								</h2>
								<p>{post.content}</p>
								<p>writer: {post.writer.displayName}</p>
								<p>
									 :
									{moment(post.createdAt).format('YYYY MMMM Do, a hh:mm:ss')}
								</p>
								<p> : {post.updatedAt}</p>
							</article>
						</React.Fragment>
					);
				})}
			</div>
		</section>
	);
}

export default List;
